<template>
  <div
    :class="custom_class"
    class="modal"
    v-if="isModalOpen"
    :style="modalStyle"
  >
    <div class="modal-title">
      <span><slot name="title-first">標</slot></span
      ><slot name="title-last">題</slot>
    </div>
    <div class="spacer"></div>
    <div class="modal-content">
      <slot name="content">內容</slot>
    </div>
  </div>
  <div class="modal-mask" @click="closeModal" v-if="isModalOpen"></div>
</template>
<script>
export default {
  props: ["isModalOpen", "modalStyle", "custom_class"],
  emits: ["close"],
  watch: {
    isModalOpen() {
      if (this.isModalOpen) {
        document.body.classList.add("modal-open");
      }
    },
  },
  methods: {
    closeModal() {
      this.$emit("close");
      document.body.classList.remove("modal-open");
    },
  },
};
</script>

