import { createStore } from "vuex";
import sfcoin from "./modules/sfcoin";

export default createStore({
  state: {
    nowAt: "outside",
    selectedOptionalSets: [],
  },
  getters: {
    selectedOptionalSets: (state) => {
      return state.selectedOptionalSets;
    },
  },
  mutations: {
    toInside(state) {
      state.nowAt = "inside";
    },
    toOutside(state) {
      state.nowAt = "outside";
    },
    toPage(state) {
      state.nowAt = "page";
    },
    setselectedOptionalSets(state, set) {
      if (!state.selectedOptionalSets.includes(set)) {
        state.selectedOptionalSets.push(set);
      }
    },
  },
  actions: {
    toInside(context) {
      context.commit("toInside");
    },
    toOutside(context) {
      context.commit("toOutside");
    },
    toPage(context) {
      context.commit("toPage");
    },
    setselectedOptionalSets(context, set) {
      context.commit("setselectedOptionalSets", set);
    },
  },
  modules: {
    sfcoin,
  },
});
