<template>
  <div class="counter">
    <button class="mr-1" @click="counter_value--">
      <img src="@/assets/imgs/minute.svg" alt="" />
    </button>
    <div class="value mr-1">
      <fcoin-value>{{ counter_value }}</fcoin-value>
    </div>
    <button @click="counter_value++">
      <img src="@/assets/imgs/plus.svg" alt="" />
    </button>
  </div>
</template>
<script>
export default {
  props: ["value"],
  created() {
    this.counter_value = this.value;
    this.$watch("counter_value", () => {
      this.$bus.$emit("update_counter_value", this.counter_value);
    });
  },
  data() {
    return {
      counter_value: 0,
    };
  },
};
</script>

