<template>
  <div class="timer">
    <!-- {{nowSec}} -->
    <img
      src="@/assets/imgs/clock.png"
      alt=""
      :class="{ flicker: isClockFlicker }"
    />
    <div class="bar">
      <div
        class="bar-in"
        :class="{ countdown: isCountdown }"
        :style="{ width: `${this.barWidth}%` }"
      ></div>
    </div>
  </div>
</template>
<script>
export default {
  props: ["set"],
  data() {
    return {
      isCountdown: true,
      countdownSec: "",
      nowSec: "",
      isClockFlicker: false,
    };
  },
  computed: {
    barWidth() {
      let barWidth = 100 * (+this.nowSec / +this.countdownSec);
      return barWidth;
    },
  },
  mounted() {
    this.countdownSec = this.set;
    this.nowSec = this.set;
    this.init();
  },
  methods: {
    init() {
      setInterval(() => {
        this.countdown();
      }, 200);
    },
    countdown() {
      this.nowSec -= 0.2;
    },
    flickerClock() {
      this.isClockFlicker = true;
      setTimeout(() => {
        this.isClockFlicker = false;
      }, 300);
    },
  },
  watch: {
    nowSec(sec) {
      if (sec <= 0) {
        this.nowSec = this.set;
        this.flickerClock();
        this.$emit("timesUp");
      }
    },
  },
};
</script>
<style lang="scss" scoped>
.timer {
  display: flex;
  align-items: center;
  img {
    width: 42px;
    height: auto;
    filter: drop-shadow(0px 0px 0px rgba(map-get($colors, "green"), 0));
    transition: all 0.3s linear;
    &.flicker {
      filter: drop-shadow(0px 0px 15px rgba(map-get($colors, "green"), 1));
      transform: scale(1.2) rotate(-10deg);
    }
  }
  .bar {
    width: 200px;
    height: 5px;
    background: rgba($color: map-get($colors, "dark"), $alpha: 0.92);
    border-radius: 50px;
    margin-left: 0.5rem;
    overflow: hidden;
    &-in {
      width: 200px;
      height: 5px;
      background: map-get($colors, "green");
      border-radius: 50px;
      transition: width 0.2s linear;
    }
  }
}
</style>
