<template>
  <footer>
    <router-link to="/" @click="toOutside()" class="icon logout">
      <span class="material-icons material-symbols-outlined"> logout </span>
    </router-link>
    <router-link :to="{ path: '/' }" @click="toInside()" class="index-btn">
      <img src="../assets/imgs/fcoin_pink.svg" />
    </router-link>
    <div class="d-flex flex-center">
      <div class="nav" v-if="nav">
        <router-link
          :to="item.to"
          class="nav-item"
          v-for="(item, index) in nav"
          :key="index"
          v-on="item.modal ? { click: clickHandler } : {}"
        >
          <img :src="item.icon" alt="" />
          <div class="text">{{ item.text }}</div>
        </router-link>
      </div>
      <a class="icon menu">
        <span class="material-icons material-icons-outlined"> menu </span>
      </a>
    </div>
  </footer>
</template>
<script>
export default {
  props: ["nav"],
  methods: {
    toInside() {
      document.body.classList.remove("modal-open");
      this.$store.dispatch("toInside");
    },
    toOutside() {
      document.body.classList.remove("modal-open");
      this.$store.dispatch("toOutside");
    },
    clickHandler() {
      this.$emit("openModal");
    },
  },
};
</script>
