<script>
import moreBtn from "@/components/MoreBtn.vue";
import { Swiper, SwiperSlide } from "swiper/vue/swiper-vue";
import "swiper/swiper-bundle.css";
import { Autoplay, Pagination } from "swiper";
export default {
  components: {
    Swiper,
    SwiperSlide,
    moreBtn,
  },
  setup() {
    return {
      modules: [Autoplay, Pagination],
    };
  },

  data() {
    return {
      sfcoin_way_to_get: [],
      active_coin_id: "demon001",
      active_coin: {},
    };
  },
  created() {
    this.sfcoin_way_to_get = this.$store.getters["sfcoin/SFCoinWaysToGet"];
    this.active_coin = this.sfcoin_way_to_get.filter(
      (el) => el.id == this.active_coin_id
    )[0];
  },
  watch: {
    active_coin_id: {
      handler(id) {
        this.active_coin = this.sfcoin_way_to_get.filter(
          (el) => el.id == id
        )[0];
      },
      immediate: false,
    },
  },
};
</script>

