<template>
  <div class="backpack-tickets">
    <content-title class="mb-6 mt-0">
      <template v-slot:primary>我</template>
      <template v-slot:text>所擁有的票券(3 / 999)</template>
    </content-title>
    <div class="tickets-content">
      <div
        class="item"
        v-for="(item, index) in tickets"
        :key="index"
        @click="openModal(item)"
      >
        <img class="item-img" :src="item.imgUrl" alt="" />
        <div class="item-info">
          <div class="title mb-2">{{ item.title }}</div>
          <div class="description">{{ item.des }}</div>
          <div class="date mt-auto ml-auto">到期日：{{ item.date }}</div>
        </div>
        <alert></alert>
      </div>
    </div>
  </div>
  <circle-bg class="spread"></circle-bg>
  <img class="backpack-bg" src="@/assets/imgs/money_bag.png" alt="" />
  <modal
    :isModalOpen="isModalOpen"
    :modalStyle="modalStyle"
    @close="closeModal"
  >
    <template v-slot:title-first>{{ modalTicket.titleFirst }}</template>
    <template v-slot:title-last>{{ modalTicket.titleLast }}</template>
    <template v-slot:content>
      <div class="star-content">
        <div>請將QR碼出示給門市人員兌換</div>
        <div class="mt-6">
          <img class="w-100" :src="modalTicket.qrCode" alt="" />
        </div>
      </div>
    </template>
  </modal>
</template>
<script>
import Alert from "@/components/Alert";
import modal from "@/components/Modal.vue";
export default {
  components: { Alert, modal },
  data() {
    return {
      tickets: [
        {
          imgUrl: require("@/assets/imgs/iphone.png"),
          title: "APPLE IPHONE 13 兌換券",
          des: "特殊幣兌換活動獎項",
          date: "2023/03/09",
          qrCode: require("@/assets/imgs/qrcode.png"),
        },
        {
          imgUrl: require("@/assets/imgs/black_hammer.png"),
          title: "采柔陶瓷不鏽鋼保溫杯兌換券",
          des: "特殊幣兌換活動獎項",
          date: "2023/03/09",
          qrCode: require("@/assets/imgs/qrcode.png"),
        },
        {
          imgUrl: require("@/assets/imgs/coffee.png"),
          title: "跨超商咖啡寄杯輕享組兌換券",
          des: "特殊幣兌換活動獎項",
          date: "2023/03/09",
          qrCode: require("@/assets/imgs/qrcode.png"),
        },
      ],
      isModalOpen: false,
      modalStyle: {
        width: "544px",
      },
      modalTicket: {
        titleFirst: "",
        titleLast: "",
        qrCode: "",
      },
    };
  },
  methods: {
    openModal(item) {
      this.modalTicket.qrCode = item.qrCode;
      let strFirst = item.title.split("")[0];
      let strLast = item.title.split("").slice(1).join("");
      this.modalTicket.titleFirst = strFirst;
      this.modalTicket.titleLast = strLast;
      this.isModalOpen = true;
    },
    closeModal() {
      this.isModalOpen = false;
    },
  },
};
</script>
