<template>
  <div class="shopping-shopping">
    <div v-for="category in filterChannels" :key="category.id">
      <div class="d-flex flex-center justify-between mb-4">
        <content-title class="mt-0 mb-0">
          <template v-slot:primary>{{ category.name.twFirst }}</template>
          <template v-slot:text>
            {{ category.name.twLast }}
            <span class="title-en">{{ category.name.en }}</span>
          </template>
        </content-title>
        <more-btn to="/Shopping/All">Back</more-btn>
      </div>
      <div class="channels">
        <router-link
          class="channel"
          v-for="channel in category.channels"
          :key="channel.id"
          :to="channel.to"
        >
          <img class="w-100 align-middle" :src="channel.imgUrl" alt="" />
        </router-link>
      </div>
      <div class="spacer my-9"></div>
    </div>
  </div>
</template>
<script>
import moreBtn from "@/components/MoreBtn.vue";
export default {
  props: ["channels"],
  components: { moreBtn },
  computed: {
    filterChannels() {
      return this.channels.filter(function (item) {
        return item.category == "Shopping";
      });
    },
  },
};
</script>
