<template>
  <modal
    :isModalOpen="is_modal_open"
    :modalStyle="modal_style"
    @close="closeModal"
  >
    <template v-slot:title-first>委</template>
    <template v-slot:title-last>買</template>
    <template v-slot:content>
      <div class="content">
        <div>請選擇要委買的金額</div>
        <div class="py-9">
          <counter :value="value"></counter>
        </div>
        <div>
          <modal-btn @click="closeModal" class="mx-2 secondary">取消</modal-btn>
          <modal-btn @click="closeModal" class="mx-2">委買</modal-btn>
        </div>
      </div>
    </template>
  </modal>
</template>
<script>
import modal from "@/components/Modal.vue";
import modalBtn from "@/components/ModalBtn.vue";
import counter from "@/components/Counter.vue";
export default {
  components: { modal, counter, modalBtn },
  props: ["is_modal_open", "value"],
  data() {
    return {
      modal_style: {
        width: "500px",
      },
    };
  },
  methods: {
    closeModal() {
      this.$emit("close");
      document.body.classList.remove("modal-open");
    },
  },
};
</script>
