<template>
  <modal
    :isModalOpen="is_modal_open"
    :modalStyle="modal_style"
    @close="closeModal"
  >
    <template v-slot:title-first>買</template>
    <template v-slot:title-last>進</template>
    <template v-slot:content>
      <div class="content">
        <div>確定買進「{{ series.series_name }}-{{ coin_data.name }}」？</div>
        <div class="py-9 d-flex flex-center justify-center">
          <fcoin-value>{{ value }}</fcoin-value>
          <img class="mx-6" src="@/assets/imgs/arrow_black.svg" alt="" />
          <img class="coin" :src="coin_data.pic_url" alt="" />
        </div>
        <div>
          <modal-btn @click="closeModal" class="mx-2 secondary">取消</modal-btn>
          <modal-btn @click="closeModal" class="mx-2">買進</modal-btn>
        </div>
      </div>
    </template>
  </modal>
</template>
<script>
import modal from "@/components/Modal.vue";
import modalBtn from "@/components/ModalBtn.vue";
export default {
  components: { modal, modalBtn },
  props: ["is_modal_open", "value", "series", "coin_data"],
  data() {
    return {
      modal_style: {
        width: "500px",
      },
    };
  },
  methods: {
    closeModal() {
      this.$emit("close");
      document.body.classList.remove("modal-open");
    },
  },
};
</script>
