        :class="{ finished: item.status == 'success' }"
      >
        <img class="coin mr-4" :src="item.pic_url" alt="" />
        <div class="mr-4 flex-1" style="width: 95px">
          <div class="name">{{ item.name }}</div>
          <div class="series">{{ item.series }}</div>
        </div>
        <div class="mr-4 d-flex flex-center" style="width: 135px">
          <span v-if="item.type == 'entrustment_sell'" class="text">委賣</span>
          <span v-if="item.type == 'entrustment_buy'" class="text">委買</span>
          <fcoin-value class="ml-2">
            <span v-if="item.entrustment_price == 0">--</span>
            <span v-else>
              {{ item.entrustment_price }}
            </span>
          </fcoin-value>
        </div>
        <div class="mr-4 d-flex flex-center" style="width: 135px">
          <span class="text">成交</span>
          <fcoin-value class="ml-2">
            <span v-if="item.deal_price == 0"> --</span>
            <span v-else>
              {{ item.deal_price }}
            </span>
          </fcoin-value>
        </div>
        <div class="mr-4 time" style="width: 100px">
          <div class="mb-2 text-right">{{ item.time.build_time }} 建立</div>
          <div class="text-right">
            <span v-if="item.time.deal_time == ''"> --</span>
            <span v-else>{{ item.time.deal_time }}</span>
            成交
          </div>
        </div>
        <div v-if="item.status == 'success'" class="status">
          <span class="success"> <span>交</span>易成功 </span>
        </div>
        <div
          v-if="item.status == 'entrustment_buy'"
          class="status"
          :class="{ 'entrustment-buy': item.status == 'entrustment_buy' }"
          @click="conformCancelEntrustment(item)"
        >
          委買中
        </div>
        <div
          v-if="item.status == 'entrustment_sell'"
          class="status"
          :class="{ 'entrustment-sell': item.status == 'entrustment_sell' }"
          @click="conformCancelEntrustment(item)"
        >
          委賣中
        </div>
      </div>
    </div>
    <modal
      :isModalOpen="is_cancel_modal_open"
      :modalStyle="cancel_modal_style"
      @close="close_cancel_modal"
    >
      <template v-slot:title-first>取</template>
      <template v-slot:title-last>消委託</template>
      <template v-slot:content>
        <div class="star-content">
          <div class="mb-9">是否取消「{{ cancel_item.name }}」的委託</div>
          <div>
            <modal-btn @click="close_cancel_modal()" class="secondary mx-2"
              >否</modal-btn
            >
            <modal-btn
              @click="
                cancelEntrustment(cancel_item);
                close_cancel_modal();
              "
              class="mx-2"
              >是</modal-btn
            >
          </div>
        </div>
      </template>
    </modal>
    <!-- 委買委賣 -->
    <modal
      :isModalOpen="is_entrustment_modal_open"
      :modalStyle="entrustment_modal_style"
      @close="close_entrustment_modal"
    >
      <template v-slot:title-first>委</template>
      <template v-slot:title-last>買委賣</template>
      <template v-slot:content>
        <div class="">
          <div class="mb-9">請選擇委買或委賣</div>
          <div class="d-flex flex-center justify-center">
            <modal-btn
              @click="
                close_entrustment_modal();
                entrustment('entrustment_buy');
              "
              class="btn-large btn-primary mx-2"
              >委買
            </modal-btn>
            <modal-btn
              @click="
                close_entrustment_modal();
                entrustment('entrustment_sell');
              "
              class="btn-large btn-blue mx-2"
              >委賣</modal-btn
            >
          </div>
        </div>
      </template>
    </modal>

    <!-- 選擇系列 -->
    <modal
      :isModalOpen="is_entrustment_series_modal_open"
      :modalStyle="entrustment_modal_style"
      @close="close_entrustment_series_modal()"
    >
      <template v-slot:title-first>委</template>
      <template v-slot:title-last
        ><span v-if="new_entrustment.status == 'entrustment_buy'">買</span>
        <span v-else-if="new_entrustment.status == 'entrustment_sell'">賣</span>
      </template>
      <template v-slot:content>
        <div class="star-content">
          <div>
            請選擇委<span v-if="new_entrustment.status == 'entrustment_buy'"
              >買</span
            ><span v-else-if="new_entrustment.status == 'entrustment_sell'"
              >賣</span
            >組合
          </div>
          <div class="mt-9">
            <div
              class="btn-black mb-4"
              @click="
                add_entrustment_series('demon');
                close_entrustment_series_modal();
                open_entrustment_coin_modal();
              "
            >
              鬼滅之刃
            </div>
            <div
              class="btn-black mb-4"
              @click="
                add_entrustment_series('sumikko');
                close_entrustment_series_modal();
                open_entrustment_coin_modal();
              "
            >
              角落生物
            </div>
            <div
              class="btn-black"
              @click="
                add_entrustment_series('fet');
                close_entrustment_series_modal();
                open_entrustment_coin_modal();
              "
            >
              遠傳電信
            </div>
          </div>
        </div>
      </template>
    </modal>
    <!-- 選擇硬幣 -->
    <modal
      :isModalOpen="is_entrustment_coin_modal_open"
      :modalStyle="entrustment_coin_modal_style"
      @close="close_entrustment_coin_modal()"
    >
      <template v-slot:title-first>委</template>
      <template v-slot:title-last
        ><span v-if="new_entrustment.status == 'entrustment_buy'">買</span>
        <span v-else-if="new_entrustment.status == 'entrustment_sell'">賣</span>
      </template>
      <template v-slot:content>
        <div class="star-content">
          <div>
            請選擇委<span v-if="new_entrustment.status == 'entrustment_buy'"
              >買</span
            ><span v-else-if="new_entrustment.status == 'entrustment_sell'"
              >賣</span
            >項目
          </div>
          <div class="my-9 d-flex flex-center justify-center">
            <div
              class="mx-3 position-relative"
              v-for="item in new_entrustment_series_items"
              :key="item.id"
              @click="chooseCoin(item)"
            >
              <img
                v-if="this.new_entrustment.name == item.name"
                src="@/assets/imgs/check_green.svg"
                alt=""
                class="coin-checked"
              />
              <img class="coin-large" :src="item.pic_url" alt="" />
            </div>
          </div>
          <div class="d-flex flex-center justify-center">
            <modal-btn
              @click="
                close_entrustment_coin_modal();
                open_entrustment_series_modal();
              "
              class="mx-2 secondary"
              >上一步
            </modal-btn>
            <modal-btn
              @click="
                close_entrustment_coin_modal();
                open_entrustment_price_modal();
              "
              class="mx-2"
              >選擇</modal-btn
            >
          </div>
        </div>
      </template>
    </modal>
    <!-- 選擇金額 -->
    <modal
      :isModalOpen="is_entrustment_price_modal_open"
      :modalStyle="entrustment_modal_style"
      @close="close_entrustment_price_modal()"
    >
      <template v-slot:title-first>委</template>
      <template v-slot:title-last
        ><span v-if="new_entrustment.status == 'entrustment_buy'">買</span>
        <span v-else-if="new_entrustment.status == 'entrustment_sell'">賣</span>
      </template>
      <template v-slot:content>
        <div class="star-content">
          <div>
            請選擇委<span v-if="new_entrustment.status == 'entrustment_buy'"
              >買</span
            ><span v-else-if="new_entrustment.status == 'entrustment_sell'"
              >賣</span
            >金額
          </div>
          <div class="my-9">
            <template v-if="new_entrustment.status == 'entrustment_sell'">
              <div class="d-flex flex-center justify-center mb-9">
                <img class="coin" :src="new_entrustment.pic_url" alt="" />
                <img class="mx-6" src="@/assets/imgs/arrow_black.svg" alt="" />
                <fcoin-value>{{
                  new_entrustment.entrustment_price
                }}</fcoin-value>
              </div>
            </template>
            <template v-else>
              <div class="d-flex flex-center justify-center mb-9">
                <fcoin-value>{{
                  new_entrustment.entrustment_price
                }}</fcoin-value>
                <img class="mx-6" src="@/assets/imgs/arrow_black.svg" alt="" />
                <img class="coin" :src="new_entrustment.pic_url" alt="" />
              </div>
            </template>
            <div>
              <counter :value="new_entrustment.entrustment_price"></counter>
            </div>
          </div>
          <div class="d-flex flex-center justify-center">
            <modal-btn
              @click="
                close_entrustment_price_modal();
                open_entrustment_coin_modal();
              "
              class="mx-2 secondary"
              >上一步
            </modal-btn>
            <modal-btn
              @click="
                close_entrustment_price_modal();
                add_entrustment_item();
              "
              class="mx-2"
            >
              委<span v-if="new_entrustment.type == 'entrustment_buy'">買</span
              ><span v-else-if="new_entrustment.type == 'entrustment_sell'"
                >賣</span
              >
