</template>

<script>
import modal from "@/components/Modal.vue";
import modalBtn from "@/components/ModalBtn.vue";
import counter from "@/components/Counter.vue";
export default {
  props: ["is_entrustment_modal_open"],
  components: { modal, modalBtn, counter },
  created() {
    this.getSFcoinEntrustmentList();
    this.filter_by = this.$route.params.filterby;
    this.$bus.$on("update_counter_value", (price) => {
      this.new_entrustment.entrustment_price = price;
    });
  },
  data() {
    return {
      tabs: [
        {
          text: "全部",
          href: { name: "exchange-entrustment", params: { filterby: "" } },
        },
        {
          text: "委買",
          href: { name: "exchange-entrustment", params: { filterby: "Buy" } },
        },
        {
          text: "委賣",
          href: "/Exchange/Entrustment/Sell",
        },
        {
          text: "已完成",
          href: "/Exchange/Entrustment/Finished",
        },
      ],
      sfcoin_entrustment_list: [],
      cancel_modal_style: {
        width: "500px",
      },
      entrustment_modal_style: {
        width: "500px",
      },
      entrustment_coin_modal_style: {
        width: "668px",
      },
      is_cancel_modal_open: false,
      is_entrustment_series_modal_open: false,
      is_entrustment_coin_modal_open: false,
      is_entrustment_price_modal_open: false,
      cancel_item: {},
      filter_by: "",
      new_entrustment: {
        type: "",
        name: "",
        series: "",
        pic_url: "",
        entrustment_price: 0,
        deal_price: 0,
        time: {
          build_time: "14:31:36",
          deal_time: "",
        },
        status: "",
      },
      new_entrustment_series_items: {},
    };
  },
  methods: {
    getSFcoinEntrustmentList() {
      this.sfcoin_entrustment_list =
        this.$store.getters["sfcoin/SFcoinEntrustmentList"];
    },
    conformCancelEntrustment(item) {
      this.open_cancle_modal();
      this.cancel_item = item;
    },
    open_cancle_modal() {
      this.is_cancel_modal_open = true;
    },
    close_cancel_modal() {
      this.is_cancel_modal_open = false;
    },
    cancelEntrustment(cancel_item) {
      this.$store.dispatch("sfcoin/cancelEntrustment", cancel_item);
    },
    close_entrustment_modal() {
      this.$emit("close_entrustment_modal");
    },
    entrustment(type) {
      this.new_entrustment.status = type;
      this.new_entrustment.type = type;
      this.open_entrustment_series_modal();
    },

    close_entrustment_series_modal() {
      this.is_entrustment_series_modal_open = false;
    },

    open_entrustment_series_modal() {
      this.is_entrustment_series_modal_open = true;
    },
    close_entrustment_coin_modal() {
      this.is_entrustment_coin_modal_open = false;
    },
    open_entrustment_coin_modal() {
      this.is_entrustment_coin_modal_open = true;
    },
    close_entrustment_price_modal() {
      this.is_entrustment_price_modal_open = false;
      document.body.classList.remove("modal-open");
    },
    open_entrustment_price_modal() {
      this.is_entrustment_price_modal_open = true;
    },
    add_entrustment_series(series) {
      let new_series = this.$store.getters["sfcoin/SFcoinSeriesById"](series);
      this.new_entrustment.series = new_series.series_name;
      this.new_entrustment_series_items = new_series.series_items;
      this.new_entrustment.name = this.new_entrustment_series_items[0].name;
      this.new_entrustment.pic_url =
        this.new_entrustment_series_items[0].pic_url;
    },
    chooseCoin(item) {
      this.new_entrustment.name = item.name;
      this.new_entrustment.pic_url = item.pic_url;
    },
    updatePriceValue() {
      console.log("trigger");
    },
    add_entrustment_item() {
      const today = new Date();
      const time =
        today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds();
      this.new_entrustment.time.build_time = time;
      this.$store.dispatch("sfcoin/addEntrustment", this.new_entrustment);
      this.new_entrustment = {
        type: "",
        name: "",
        series: "",
        pic_url: "",
        entrustment_price: 0,
        deal_price: 0,
        time: {
          build_time: "14:31:36",
          deal_time: "",
        },
        status: "",
      };
    },
  },
  computed: {
    filter_sfcoin_entrustment_list() {
      if (this.filter_by == "Buy") {
        return this.sfcoin_entrustment_list.filter(
          (el) => el.status == "entrustment_buy"
        );
      } else if (this.filter_by == "Sell") {
        return this.sfcoin_entrustment_list.filter(
          (el) => el.status == "entrustment_sell"
        );
      } else if (this.filter_by == "Finished") {
        return this.sfcoin_entrustment_list.filter(
          (el) => el.status == "success"
        );
      } else return this.sfcoin_entrustment_list;
    },
  },
  beforeUnmount: function () {
    // 元件銷毀前要註銷監聽事件
    this.$bus.$off("update_counter_value");
  },

  watch: {
    $route() {
      this.filter_by = this.$route.params.filterby;
    },
  },
};
</script>
