<template>
  <div class="index">
    <layout-header
      v-if="nowAt == 'inside' || nowAt == 'outside'"
    ></layout-header>
    <outside
      :class="{ show: nowAt == 'outside' }"
      @toInside="toInside"
    ></outside>
    <inside
      :class="{ show: nowAt == 'inside', opacity: nowAt == 'page' }"
      @toPage="toPage"
    ></inside>
    <router-view></router-view>
    <layout-footer
      v-if="nowAt == 'inside' || nowAt == 'outside'"
    ></layout-footer>
    <mountain-bg
      :class="{ opacity: nowAt == 'inside', hide: nowAt == 'page' }"
    ></mountain-bg>
    <circle-bg
      :class="{ spread: nowAt == 'inside' || nowAt == 'page' }"
    ></circle-bg>
    <radio-circle
      :class="{ focus: nowAt == 'inside', hide: nowAt == 'page' }"
    ></radio-circle>
  </div>
</template>
<script>
import radioCircle from "../components/RadioCircle.vue";
export default {
  components: { radioCircle },

  data() {
    return {};
  },
  computed: {
    nowAt() {
      return this.$store.state.nowAt;
    },
  },
  methods: {
    toInside() {
      this.$store.dispatch("toInside");
    },
    toOutside() {
      this.$store.dispatch("toOutside");
    },
    toPage() {
      this.$store.dispatch("toPage");
    },
  },
};
</script>
