<template>
  <div class="exchange-gift flex-1">
    <content-tabs :tabs="tabs"></content-tabs>
    <content-title class="mb-6 mt-0">
      <template v-slot:primary>{{ first_series_name }}</template>
      <template v-slot:text>{{ last_series_name }}</template>
    </content-title>
    <div
      class="card-bg d-flex flex-center justify-center pa-4 position-relative mb-9"
    >
      <div
        class="series-item mx-3"
        v-for="item in this.my_coins.series_items"
        :key="item.id"
      >
        <img class="coin-large" :src="item.pic_url" alt="" />
        <span class="number">x{{ item.amount }}</span>
      </div>
    </div>
    <content-sub-title>活動兌換</content-sub-title>
    <div class="card-bg mb-9">
      <div
        class="d-flex flex-center justify-between pa-4 exchange-item"
        v-for="(item, index) in gift_exchange_list.exchange_list"
        :key="index"
        :class="{
          disabled: !my_coins.series_items.every(
            (el, i) => el.amount >= item.required[i].amount
          ),
        }"
      >
        <div class="d-flex flex-center">
          <div
            class="series-item-small mx-2"
            v-for="coin in this.my_coins.series_items"
            :key="coin.id"
            :class="{
              disabled:
                item.required[item.required.findIndex((el) => el.id == coin.id)]
                  .amount == 0,
            }"
          >
            <img class="coin" :src="coin.pic_url" alt="" />
            <span class="number">
              <span
                :class="{
                  green:
                    coin.amount >=
                    item.required[
                      item.required.findIndex((el) => el.id == coin.id)
                    ].amount,
                }"
                >{{ coin.amount }}</span
              >
              /{{
                item.required[item.required.findIndex((el) => el.id == coin.id)]
                  .amount
              }}</span
            >
          </div>
        </div>
        <img class="" src="@/assets/imgs/arrow_black.svg" alt="" />
        <img
          class="gift-img"
          :src="item.gift.pic"
          alt=""
          @click="open_gift_modal(item.gift)"
        />
        <div class="exchange-btn">
          <alert
            class="green"
            v-if="
              my_coins.series_items.every(
                (el, i) => el.amount >= item.required[i].amount
              )
            "
          ></alert>
          <button
            @click="open_exchange_modal(item, this.my_coins.series_items)"
          >
            兌換
          </button>
        </div>
      </div>
    </div>
    <content-sub-title>活動說明</content-sub-title>
    <div class="card-bg">
      <img
        class="w-100 align-middle"
        :src="gift_exchange_list.series_campaign_info.pic_url"
        alt=""
      />
      <div
        class="pa-9"
        style="font-size: 1.125rem"
        v-html="gift_exchange_list.series_campaign_info.info_des"
      ></div>
    </div>
    <!-- 檢視禮品 -->
    <modal
      :isModalOpen="is_gift_modal_open"
      :modalStyle="gift_modal_style"
      @close="close_gift_modal"
    >
      <template v-slot:title-first>檢</template>
      <template v-slot:title-last>視禮品</template>
      <template v-slot:content>
        <div class="gift-modal">
          <div class="mb-9">{{ gift_modal_info.name }}</div>
          <div class="d-flex flex-center justify-center">
            <img :src="gift_modal_info.pic" alt="" />
          </div>
        </div>
      </template>
    </modal>
    <!-- 兌換 -->
    <modal
      :isModalOpen="is_exchange_modal_open"
      :modalStyle="exchange_modal_style"
      @close="close_exchange_modal"
    >
      <template v-slot:title-first>活</template>
      <template v-slot:title-last>動兌換</template>
      <template v-slot:content>
        <div class="exchange-modal">
          <div class="mb-9">
            是否兌換「{{ exchange_modal_info.gift.name }} 兌換券」
          </div>
          <div
            class="d-flex flex-center justify-center exchange-item disabled in-modal mx-4 mb-9"
          >
            <div
              class="d-flex flex-center mr-9"
              style="margin-left: -0.5rem; margin-right: -0.5rem"
            >
              <div
                class="series-item-small mx-2"
                v-for="(coin, index) in this.exchange_modal_info.coins.filter(
                  (el) => el.amount >= el.required && el.required !== 0
                )"
                :key="index"
              >
                <img class="coin" :src="coin.url" alt="" />
                <span class="number">
                  <span class="green">{{ coin.amount }}</span>
                  /{{ coin.required }}</span
                >
              </div>
            </div>
            <img class="mr-9" src="@/assets/imgs/arrow_black.svg" alt="" />
            <img class="gift-img" :src="exchange_modal_info.gift.pic" alt="" />
          </div>
          <div>
            <modal-btn @click="close_exchange_modal" class="mx-2 secondary"
              >取消</modal-btn
            >
            <modal-btn @click="close_exchange_modal" class="mx-2"
              >兌換</modal-btn
            >
          </div>
        </div>
      </template>
    </modal>
  </div>
</template>

<script>
import modal from "@/components/Modal.vue";
import modalBtn from "@/components/ModalBtn.vue";
import Alert from "@/components/Alert";
import { reactive } from "@vue/reactivity";

export default {
  components: { Alert, modal, modalBtn },

  created() {
    this.init();
  },
  setup() {
    const exchange_modal_info = reactive({ gift: {}, coins: [] });
    return {
      exchange_modal_info,
    };
  },
  data() {
    return {
      tabs: [
        {
          text: "鬼滅之刃",
          href: "/Exchange/Gift/demon",
        },
        {
          text: "角落生物",
          href: "/Exchange/Gift/sumikko",
        },
        {
          text: "遠傳電信",
          href: "/Exchange/Gift/fet",
        },
      ],
      filter_by: "",
      gift_exchange_list: [],
      my_coins: [],
      gift_modal_info: {},
      is_gift_modal_open: false,
      is_exchange_modal_open: false,
      gift_modal_style: {
        width: "550px",
      },
      exchange_modal_style: {
        width: "700px",
      },
    };
  },
  computed: {
    first_series_name() {
      return this.gift_exchange_list.series_name.split("")[0];
    },
    last_series_name() {
      return this.gift_exchange_list.series_name.split("").slice(1).join("");
    },
  },
  methods: {
    init() {
      this.filter_by = this.$route.params.filterby;
      this.get_gift_exchange_list();
      this.get_my_coins();
    },
    get_gift_exchange_list() {
      this.gift_exchange_list = this.$store.getters[
        "sfcoin/GiftExchangeList"
      ].filter((el) => el.series_id == this.filter_by)[0];
    },
    get_my_coins() {
      this.my_coins = this.$store.getters["sfcoin/MyCoins"].filter(
        (el) => el.series_id == this.filter_by
      )[0];
    },
    open_gift_modal(gift) {
      this.gift_modal_info = gift;
      this.is_gift_modal_open = true;
    },
    close_gift_modal() {
      this.is_gift_modal_open = false;
      this.gift_modal_info = {};
      document.body.classList.remove("modal-open");
    },
    open_exchange_modal(item, coin) {
      // console.log(item,coin);
      this.exchange_modal_info.gift = item.gift;
      coin.forEach((coin, i) => {
        let coin_obj = {};
        coin_obj.url = coin.pic_url;
        coin_obj.amount = coin.amount;
        coin_obj.required = item.required[i].amount;
        this.exchange_modal_info.coins.push(coin_obj);
      });
      this.is_exchange_modal_open = true;
    },
    close_exchange_modal() {
      this.is_exchange_modal_open = false;
      this.exchange_modal_info.gift = {};
      this.exchange_modal_info.coins = [];
      document.body.classList.remove("modal-open");
    },
  },
  watch: {
    $route() {
      this.init();
    },
  },
};
</script>
