<template>
  <div class="exchange">
    <content-header>
      <template v-slot:img>
        <img src="@/assets/imgs/icon_libra.svg" alt="" />
      </template>
      <template v-slot:name>
        <slot name="name"> <span class="red">交</span>易所 </slot>
      </template>
      <template v-slot:tabs>
        <router-link
          to="/Exchange/Exchange"
          class="tab"
          :class="{
            'router-link-exact-active':
              this.$route.name == 'exchange-exchange' ||
              this.$route.name == 'exchange-exchange-detail',
          }"
          >交易區</router-link
        >
        <router-link
          to="/Exchange/Entrustment"
          class="tab"
          :class="{
            'router-link-exact-active':
              this.$route.name == 'exchange-entrustment',
          }"
        >
          我的委託
        </router-link>
        <router-link
          to="/Exchange/Gift"
          class="tab"
          :class="{
            'router-link-exact-active': this.$route.name == 'exchange-gift',
          }"
          >活動兌換</router-link
        >
      </template>
    </content-header>
    <router-view
      :is_entrustment_modal_open="is_entrustment_modal_open"
      @close_entrustment_modal="closeModal"
    ></router-view>
    <layout-footer
      :nav="
        is_optional
          ? optional_nav
          : is_entrustment
          ? entrustment_nav
          : is_gift
          ? gift_nav
          : ''
      "
      @openModal="openModal"
    ></layout-footer>
    <modal
      :isModalOpen="is_optional_modal_open"
      :modalStyle="modal_style"
      @close="closeModal"
    >
      <template v-slot:title-first>新</template>
      <template v-slot:title-last>增商品</template>
      <template v-slot:content>
        <div class="star-content">
          <div>請選擇自選組合</div>
          <div class="mt-9">
            <div
              class="modal-button mb-4"
              @click="
                addOptionalSet('鬼滅之刃');
                closeModal();
              "
            >
              鬼滅之刃
            </div>
            <div
              class="modal-button mb-4"
              @click="
                addOptionalSet('角落生物');
                closeModal();
              "
            >
              角落生物
            </div>
            <div
              class="modal-button"
              @click="
                addOptionalSet('遠傳電信');
                closeModal();
              "
            >
              遠傳電信
            </div>
          </div>
        </div>
      </template>
    </modal>
  </div>
</template>
<script>
import modal from "@/components/Modal.vue";
export default {
  components: { modal },
  created() {
    this.getPath();
  },
  data() {
    return {
      is_optional: false,
      is_entrustment: false,
      is_gift: false,
      optional_nav: [
        {
          to: "",
          icon: require("@/assets/imgs/icon_add.svg"),
          text: "新增自選",
          modal: true,
        },
      ],
      entrustment_nav: [
        {
          to: "",
          icon: require("@/assets/imgs/icon_add.svg"),
          text: "新增委託",
          modal: true,
        },
      ],
      gift_nav: [
        {
          to: "/Backpack/SFcoinList",
          icon: require("@/assets/imgs/icon_coin_outline.svg"),
          text: "特殊幣清單",
        },
      ],
      modal_style: {
        width: "545px",
      },
      is_optional_modal_open: false,
      is_entrustment_modal_open: false,
    };
  },
  watch: {
    $route() {
      this.getPath();
    },
  },
  methods: {
    getPath() {
      if (this.$route.path !== "/") {
        this.$store.dispatch("toPage");
      }
      if (this.$route.path == "/Exchange/Exchange/Optional") {
        this.is_optional = true;
      } else {
        this.is_optional = false;
      }
      if (
        this.$route.path == "/Exchange/Entrustment" ||
        this.$route.path == "/Exchange/Entrustment/Buy" ||
        this.$route.path == "/Exchange/Entrustment/Sell" ||
        this.$route.path == "/Exchange/Entrustment/Finished"
      ) {
        this.is_entrustment = true;
      } else {
        this.is_entrustment = false;
      }
      if (
        this.$route.path == "/Exchange/Gift/demon" ||
        this.$route.path == "/Exchange/Gift/sumikko" ||
        this.$route.path == "/Exchange/Gift/fet"
      ) {
        this.is_gift = true;
      } else {
        this.is_gift = false;
      }
    },
    openModal() {
      if (this.$route.path == "/Exchange/Exchange/Optional") {
        this.is_optional_modal_open = true;
      } else {
        this.is_entrustment_modal_open = true;
      }
    },
    closeModal() {
      if (this.$route.path == "/Exchange/Exchange/Optional") {
        this.is_optional_modal_open = false;
      } else {
        this.is_entrustment_modal_open = false;
      }
      document.body.classList.remove("modal-open");
    },
    addOptionalSet(set) {
      this.$store.dispatch("setselectedOptionalSets", set);
    },
  },
};
</script>
<style lang="scss" scoped>
.exchange {
  padding: 2.25rem;
  min-height: 100vh;
  padding-top: calc(68px + 1.5rem);
  padding-bottom: calc(96px + 2rem);
  z-index: 1;
  position: relative;
  display: flex;
  flex-direction: column;
}
.modal-button {
  display: block;
  background: map-get($map: $colors, $key: dark);
  color: #fff;
  border-radius: 50px;
  height: 45px;
  display: flex;
  justify-content: center;
  align-items: center;
  @include serif;
  font-size: 1.125rem;
}
</style>
