<script>
import modal from "@/components/Modal.vue";
import entrustedBuyPriceModal from "@/components/exchange/EntrustedBuyPriceModal.vue";
import entrustedSellPriceModal from "@/components/exchange/EntrustedSellPriceModal.vue";
import entrustedBuyConfirmModal from "@/components/exchange/EntrustedBuyConfirmModal.vue";
import entrustedSellConfirmModal from "@/components/exchange/EntrustedSellConfirmModal.vue";

export default {
  components: {
    modal,
    entrustedBuyPriceModal,
    entrustedSellPriceModal,
    entrustedBuyConfirmModal,
    entrustedSellConfirmModal,
  },
  data() {
    return {
      coin_id: "",
      coin_series: "",
      series: [],
      coinData: {},
      series_info_modal_style: {
        width: "762px",
        height: "762px",
      },
      is_series_info_modal_open: false,
      is_entrusted_buy_price_modal_open: false,
      is_entrusted_sell_price_modal_open: false,
      is_entrusted_buy_confirm_modal_open: false,
      is_entrusted_sell_confirm_modal_open: false,
      buy_value: 0,
      sell_svalue: 0,
    };
  },
  created() {
    this.init();
  },

  methods: {
    init() {
      this.getCoinID();
      this.getCoinSeries();
      this.getSeriesData();
      this.getCoinData();
    },
    getCoinID() {
      // console.log(this.$route.params.id);
      this.coin_id = this.$route.params.id;
    },
    getCoinSeries() {
      this.coin_series = this.$route.params.series;
    },
    getSeriesData() {
      this.series = this.$store.getters["sfcoin/SFcoinSeries"].filter(
        (item) => item.series_id == this.coin_series
      )[0];
    },
    getCoinData() {
      this.coinData = this.series.series_items.filter(
        (item) => item.id == this.coin_id
      )[0];
    },
    closeSeriesInfoModal() {
      this.is_series_info_modal_open = false;
    },
    openSeriesInfoModal() {
      this.is_series_info_modal_open = true;
    },
    closeEntrustedBuyPriceModal() {
      this.is_entrusted_buy_price_modal_open = false;
    },
    openEntrustedBuyPriceModal() {
      this.is_entrusted_buy_price_modal_open = true;
    },
    closeEntrustedSellPriceModal() {
      this.is_entrusted_sell_price_modal_open = false;
    },
    openEntrustedSellPriceModal() {
      this.is_entrusted_sell_price_modal_open = true;
    },
    closeEntrustedBuyConfirmModal() {
      this.is_entrusted_buy_confirm_modal_open = false;
    },
    openEntrustedBuyConfirmModal(value) {
      this.is_entrusted_buy_confirm_modal_open = true;
      this.buy_value = value;
    },
    closeEntrustedSellConfirmModal() {
      this.is_entrusted_sell_confirm_modal_open = false;
    },
    openEntrustedSellConfirmModal(value) {
      this.is_entrusted_sell_confirm_modal_open = true;
      this.sell_value = value;
    },
  },
  computed: {
    first_series_name() {
      return this.series.series_name.split("")[0];
    },
    last_series_name() {
      return this.series.series_name.split("").slice(1).join("");
    },
  },
  watch: {
    "$route.params.id": {
      handler: function (value) {
        if (value) this.init();
      },
      deep: true,
      immediate: true,
    },
  },
};
</script>
