  mutations: {
    cancelEntrustment(state, item) {
      const index = state.SFcoinEntrustmentList.findIndex(
        (el) => el.name == item.name
      );

      state.SFcoinEntrustmentList.splice(index, 1);
    },
    addEntrustment(state, item) {
      state.SFcoinEntrustmentList.push(item);
    },
  },
  actions: {
    cancelEntrustment(context, item) {
      context.commit("cancelEntrustment", item);
    },
    addEntrustment(context, item) {
      context.commit("addEntrustment", item);
    },
  },
  getters: {
    SFcoinSeries(state) {
      return state.SFcoinSeries;
    },
    SFcoinSeriesById: (state) => (id) => {
      return state.SFcoinSeries.filter((el) => el.series_id == id)[0];
    },
    SFcoinEntrustmentList(state) {
      return state.SFcoinEntrustmentList;
    },
    MyCoins(state) {
      return state.MyCoins;
    },
    GiftExchangeList(state) {
      return state.GiftExchangeList;
    },
    SFCoinWaysToGet(state) {
      return state.SFCoinWaysToGet;
    },
  },
