<template>
  <div class="backpack">
    <content-header>
      <template v-slot:img>
        <img src="@/assets/imgs/icon_backpack.svg" alt="" />
      </template>
      <template v-slot:name> <span class="red">我</span>的行囊 </template>
      <template v-slot:tabs>
        <router-link to="/Backpack/FcoinDetail" class="tab">遠傳幣</router-link>
        <router-link
          to="/Backpack/SFcoinDetail"
          class="tab"
          :class="{ 'router-link-exact-active': isSFList }"
        >
          特殊幣
        </router-link>
        <router-link to="/Backpack/Tickets" class="tab">票券</router-link>
      </template>
    </content-header>
    <router-view></router-view>
    <layout-footer :nav="isSF ? SFNav : isSFList ? SFListNav : ''">
    </layout-footer>
  </div>
</template>
<script>
export default {
  created() {
    this.getPath();
  },
  data() {
    return {
      isSF: false,
      isSFList: false,
      SFNav: [
        {
          to: "/Backpack/SFcoinList",
          icon: require("@/assets/imgs/icon_coin_outline.svg"),
          text: "特殊幣清單",
        },
      ],
      SFListNav: [
        {
          to: "/Exchange/Gift/demon",
          icon: require("@/assets/imgs/icon_gift.svg"),
          text: "前往兌換",
        },
        {
          to: "/Backpack/SFcoinDetail",
          icon: require("@/assets/imgs/icon_list.svg"),
          text: "特殊幣明細",
        },
      ],
    };
  },
  watch: {
    $route() {
      this.getPath();
    },
  },
  methods: {
    getPath() {
      if (this.$route.path == "/Backpack/SFcoinDetail") {
        this.isSF = true;
      } else {
        this.isSF = false;
      }
      if (this.$route.path == "/Backpack/SFcoinList") {
        this.isSFList = true;
      } else {
        this.isSFList = false;
      }
    },
  },
};
</script>
<style lang="scss" scoped>
.backpack {
  min-height: 100vh;
  z-index: 1;
  position: relative;
}
</style>
