import { createApp } from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import Footer from "./components/Footer";
import Header from "./components/Header";
import ContentHeader from "./components/ContentHeader";
import ContentTabs from "./components/ContentTabs";
import ContentTitle from "./components/ContentTitle";
import ContentSubTitle from "./components/ContentSubTitle";
import Search from "./components/Search";
import CricleBg from "./components/CircleBg.vue";
import MountainBg from "./components/MountainBg.vue";
import Outside from "./components/Outside.vue";
import Inside from "./components/Inside.vue";
import FcoinValue from "./components/FcoinValue.vue";
import Marquee from "./components/Marquee.vue";
// overlay
import VueLoading from "vue-loading-overlay";
import "vue-loading-overlay/dist/vue-loading.css";

//mitt
import bus from "./bus";

const app = createApp(App);

// global registration - can be used anywhere
app.component("layout-footer", Footer);
app.component("layout-header", Header);
app.component("content-header", ContentHeader);
app.component("content-tabs", ContentTabs);
app.component("content-title", ContentTitle);
app.component("content-sub-title", ContentSubTitle);
app.component("search", Search);
app.component("circle-bg", CricleBg);
app.component("mountain-bg", MountainBg);
app.component("loading", VueLoading);
app.component("Outside", Outside);
app.component("Inside", Inside);
app.component("fcoin-value", FcoinValue);
app.component("marquee", Marquee);
// end global registration

//bus
app.config.globalProperties.$bus = bus;

app.use(store).use(router).mount("#app");
