<template>
  <div class="d-flex flex-column flex-1">
    <div class="d-flex flex-center justify-between mb-4">
      <content-title class="mt-0 mb-0">
        <template v-slot:primary>{{ fitlerChannel[0].name.twFirst }}</template>
        <template v-slot:text>
          {{ fitlerChannel[0].name.twLast }}
          <span class="title-en">{{ fitlerChannel[0].name.en }}</span>
        </template>
      </content-title>
      <more-btn @click="$router.go(-1)" to="/">Back</more-btn>
    </div>
    <div class="channel">
      <template v-if="$route.params.channel == 'Pxmart'">
        <img src="@/assets/imgs/px_2.png" alt="" class="d-block w-100" />
        <img src="@/assets/imgs/px_1.png" alt="" class="d-block w-100" />
      </template>
      <template v-else>
        {{ fitlerChannel[0].name.twFirst + fitlerChannel[0].name.twLast }}
      </template>
    </div>
  </div>
</template>

<script>
import moreBtn from "@/components/MoreBtn.vue";
export default {
  props: ["channels"],
  components: { moreBtn },
  computed: {
    fitlerChannel() {
      let fiterCategory = this.channels.filter((category) => {
        return category.category == this.$route.params.category;
      });
      let filterChannel = fiterCategory[0].channels.filter((channel) => {
        return channel.id == this.$route.params.channel;
      });
      return filterChannel;
    },
  },
};

